@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Monofett&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{--color: #b2e2e1}body{display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:#b2e2e1;background-color:var(--color);min-height:100vh}.App{display:flex;flex-direction:column;align-items:center}.home-info{display:flex;flex-direction:column;text-align:center;justify-content:center;font-family:sans-serif;border:2px black solid;border-radius:5px;background-color:#fddbe1;width:400px;height:300px;margin-top:50px;box-shadow:5px 5px 0px black}.home-info p{padding:0 20px 0 20px;font-size:1.2em}.home-info p a{color:black;margin:0px 5px 0 5px}.home-info p a:hover{color:#ffaebc}.project-info{display:flex;flex-direction:column;text-align:center;justify-content:center;font-family:sans-serif;border:2px black solid;border-radius:5px;background-color:#fddbe1;width:auto;max-width:300px;height:auto;margin-top:50px;box-shadow:5px 5px 0px black}.project-info p{padding:0 20px 0 20px;font-size:1.2em}.title{border:2px black solid;border-radius:5px;background-color:#fddbe1;width:auto;height:auto;box-shadow:5px 5px 0px black;padding:5px 10px 5px 10px;margin-bottom:50px;margin-top:100px}nav{position:fixed;top:0;left:0;z-index:9999;width:100%;height:70px;background-color:#fddbe1;border:2px black solid;border-radius:5px}nav ul{display:flex;flex-direction:row;flex:1 1;justify-content:space-around;list-style:none}nav ul li{border:2px black solid;border-radius:5px;background-color:#fbe7c6;width:auto;max-width:300px;height:auto;box-shadow:5px 5px 0px black;padding:5px 10px 5px 10px}nav ul li a{color:black}nav ul li a:hover{color:#ffaebc}@media only screen and (min-device-width: 375px) and (max-device-width: 667px) and (-webkit-min-device-pixel-ratio: 2){.home-info{max-width:300px}nav{width:99%;height:40px}nav ul{justify-content:space-around;padding:0;padding-top:5px;margin:0}nav ul li{max-width:10%;max-height:auto;font-size:0.5em;justify-content:center}.title{font-size:1.5em;margin-top:70px;margin-bottom:20px}}@media only screen and (min-device-width: 414px) and (max-device-width: 736px) and (-webkit-min-device-pixel-ratio: 3){.home-info{width:auto}}

#calculator{display:flex;flex-direction:column;align-items:center;justify-content:space-around;font-family:sans-serif;border:2px black solid;border-radius:5px;background-color:#ffaebc;width:360px;padding:20px 0 0 0;box-shadow:5px 5px 0px black}#calc-display{text-align:right;border:2px black solid;border-radius:5px;background-color:#a0e7e5;box-shadow:2px 2px 0px grey;margin:10px;padding:10px 20px 10px 20px;font-size:2em;line-height:1em;width:280px;overflow-wrap:break-word;word-wrap:break-word;min-height:1em}#btnpad{width:auto;display:flex;flex-direction:row;align-items:center;justify-content:flex-end;margin-bottom:20px;padding:0px}#numpad{display:flex;flex-wrap:wrap;width:200px;justify-content:flex-start}#numpad .button{background-color:#b4f8c8}#oprpad{display:flex;justify-content:flex-end;flex-wrap:wrap;width:130px}.button{border:2px black solid;border-radius:15px;background-color:#fbe7c6;box-shadow:2px 2px 0px grey;width:50px;height:50px;display:flex;align-items:center;justify-content:space-around;margin:5px}#zero{width:115px}#equals{width:115px}#clear{width:115px;background-color:#ef7c8e}#footer{font-size:0.5em;margin:5px;text-align:center}#footer p{margin:0}@media only screen and (min-device-width: 375px) and (max-device-width: 667px) and (-webkit-min-device-pixel-ratio: 2){#calculator{width:80vw}#calc-display{width:200px}.button{width:40px;height:40px}#zero{width:94px}#equals{width:94px}#clear{width:94px}#btnpad{width:280px}}

#drum-machine{display:flex;flex-direction:column;align-items:center;text-align:center;font-family:sans-serif;border:2px black solid;border-radius:5px;background-color:#ffaebc;width:200px;box-shadow:5px 5px 0px black}#padbank{text-align:center;display:flex;flex-wrap:wrap;align-items:center;justify-content:center;width:200px}.drum-pad{border:2px black solid;border-radius:15px;background-color:#fbe7c6;box-shadow:2px 2px 0px grey;width:50px;height:50px;display:flex;align-items:center;justify-content:space-around;margin:5px}#display{border:2px black solid;border-radius:5px;background-color:#a0e7e5;box-shadow:2px 2px 0px grey;margin:10px;height:50px;width:90%;display:flex;align-items:center;justify-content:space-around}.activePad{background-color:#b4f8c8}#footer{font-size:0.5em;margin:5px;text-align:center}#footer p{margin:0}

.wrapper{display:flex;flex-direction:row;padding:20px;height:auto}#editor-box{width:30vw;border-radius:10px;border:none;caret-color:white;padding-left:20px;padding-right:20px;box-shadow:5px 5px 5px black;background:#333;color:#fff}#editor-box textarea{background:transparent;border:none;color:#fff;width:100%;height:85%}#preview-box{width:30vw;padding-left:20px;padding-right:20px;background-color:white;box-shadow:5px 5px 5px black;border-radius:10px;margin-left:20px}#preview-box #preview-box code{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}#preview-box img{display:block;max-width:90%;margin:auto}@media only screen and (min-device-width: 375px) and (max-device-width: 667px) and (-webkit-min-device-pixel-ratio: 2){.wrapper{display:flex;flex-direction:column;padding:20px;height:auto}.wrapper #editor-box{width:80vw}.wrapper #preview-box{width:80vw;margin:10px 0 0 0}.wrapper code{font-size:0.75em}}

#quote-box{background-color:white;border:2px black solid;border-radius:5px;box-shadow:5px 5px 0px black;width:700px;min-height:400px;display:flex;flex-direction:column;justify-content:space-between}#quote-box #text-box{color:#5e6464;font-size:2em;font-family:"Major Mono Display", Arial, sans-serif;padding:40px;margin:20px}#quote-box #text-box #author{font-size:1.2em;font-family:Monofett, monospace;padding:20px 0 0 0;text-align:right}#quote-box #button-box{display:flex;flex-direction:row;justify-content:space-around;align-items:center;padding:50px 0 50px 0}#quote-box #button-box #new-quote{border:2px black solid;border-radius:5px;background-color:#fbe7c6;width:auto;max-width:300px;height:auto;box-shadow:5px 5px 0px black;padding:5px 10px 5px 10px;cursor:pointer;font-size:1.2em}#quote-box #button-box #tweet-quote{font-size:40px;color:#5e6464}@media only screen and (min-device-width: 375px) and (max-device-width: 667px) and (-webkit-min-device-pixel-ratio: 2){.title{width:325px}#quote-box{width:300px;min-height:auto;font-size:0.75em}#quote-box #text-box{margin:0}#quote-box #button-box{padding:0 0 10px 0}}

